<script>
  import { onMount } from "svelte";
  import Split from "split.js";

  let content = [];
  let searchVal = "";
  let searchInput;
  let selectedNav = "";
  let menuVisible = true;
  let isMobile;
  let loading = false;
  var screen = window.matchMedia("(max-width: 799px)");
  let searchVisible;

  $: filtered = content.filter((item) => {
    return item.name.includes(searchVal);
  });
  $: isMobile = screen.matches;

  screen.addEventListener("change", (e) => {
    if (e.matches) {
      if (menuVisible) menuVisible = false;
      isMobile = true;
      destroySplit();
    } else {
      isMobile = false;
      menuVisible = true;
      setTimeout(() => {
        initSplit();
      });
    }
  });

  const url =
    "https://raw.githubusercontent.com/AsafAgranat/asafagranat.github.io/master/files/TVP_george_ref.txt";

  onMount(async () => {
    const res = await fetch(url)
      .then((res) => res.text())
      .then((string) => {
        const nodes = htmlFromString(string);
        for (const [i, node] of nodes.entries()) {
          /* if node is html link */
          if (!node.nodeValue) {
            /* if entry doesnt exist yet */
            if (!content.find((entry) => entry.name === node.innerText)) {
              let entry = {
                name: nodes[i].innerText,
              };
              let brief = [];

              if (nodes[i + 1].nodeValue) {
                brief.push(nodes[i + 1].textContent);
                entry = { ...entry, brief };
              }
              content = [...content, entry];
            } else {
              let entry = content.find(
                (entry) => entry.name === node.innerText
              );
              let full = entry.full ? entry.full : [];
              if (nodes[i + 1].nodeValue) {
                /* if the text node is the full description itself */
                if (nodes[i + 1].nodeValue.indexOf("-------") !== -1) {
                  let obj = content[content.indexOf(entry)];
                  let text = nodes[i + 1].textContent.split(
                    "------------------------------------------------------------------------------------------------------"
                  );
                  if (text[1].includes("+")) {
                    content[content.indexOf(entry) + 1].version = text[1];
                  }

                  obj.full = [...full, text[0]];
                }
              }
            }
          }
        }
      });

    if (!screen.matches) {
      menuVisible = true;
      searchVisible = true;
      initSplit();
    } else {
      searchVisible = false;
      menuVisible = false;
    }
  });

  var splitInstance = {};

  function initSplit() {
    splitInstance = Split(["#nav", "#main"], {
      sizes: [25, 75],
      minSize: [100, 300],
    });
  }

  function destroySplit() {
    splitInstance.destroy();
  }

  function htmlFromString(htmlString) {
    var div = document.createElement("div");
    div.innerHTML = htmlString.trim();
    return div.childNodes;
  }

  function clearInput() {
    searchVal = "";
    if (isMobile) searchVisible = false;
  }

  function toggleMenu() {
    menuVisible = !menuVisible;
  }

  function handleKeydown(event) {
    if (isMobile) searchVisible = true;
    setTimeout(() => {
      if (
        document.activeElement.id !== "searchInput" &&
        event.keyCode >= 65 &&
        event.keyCode <= 90
      ) {
        // console.log(event.key);

        searchVal = event.key;
        searchInput.focus();
      }
    });
  }

  function handleMainClick() {
    if (!isMobile) return;
    if (menuVisible) {
      toggleMenu();
    }
  }

  function handleNavClick(name) {
    console.log(name);
    selectedNav = name;
    toggleMenu();
  }

  function handleSearchButtonClick() {
    searchVisible = true;
  }
</script>

<svelte:head>
  <title>Blah</title>
</svelte:head>
<svelte:window on:keydown={handleKeydown} />
<header>
  <div>
    {#if isMobile}
      <button class="menu-button" on:click={toggleMenu}>☰</button>
    {/if}
    <h1 class:is-mobile={isMobile}>TVPaint George Script documentation</h1>
  </div>
  <div class="search-input-wrapper" class:is-mobile={isMobile}>
    {#if isMobile && !searchVisible}
      <button class="search-button" on:click={handleSearchButtonClick}
        >🔍</button
      >
    {/if}
    {#if !isMobile || (isMobile && searchVisible)}
      <input
        type="text"
        bind:value={searchVal}
        bind:this={searchInput}
        class="search-input"
        class:is-mobile={isMobile}
        id="searchInput"
        placeholder="Type here to filter the docs"
      />
      <button class="clear-input" on:click={clearInput}>✕</button>
    {/if}
  </div>
</header>
<div class="wrapper">
  {#if menuVisible}
    <nav id="nav" class:is-mobile={isMobile}>
      <div class="nav-inner">
        {#each filtered as c}
          <a
            href={`#${c.name}`}
            class:selected={selectedNav === c.name}
            on:click={handleNavClick(c.name)}
            ><span class="link-prefix">tv_</span>{c.name.replace("tv_", "")}</a
          >
        {/each}
      </div>
    </nav>
  {/if}

  <main id="main" class:is-blocked={menuVisible} on:click={handleMainClick}>
    {#if loading}
      <p>loading!!!!!!!</p>
    {/if}

    {#each filtered as c}
      <div class="anchor" id={c.name} />
      <article>
        <div class="article-header">
          <h2>
            {c.name}
          </h2>
          {#if c.version}<span class="article-version">{c.version}</span>{/if}
        </div>
        <div class="article-content">
          {#if c.full}
            {#each c.full as full}
              <div>
                <pre>					
					{full}
				</pre>
              </div>
            {/each}
          {/if}
        </div>
      </article>
      <!-- {:else}
  Nothing to do here! -->
    {/each}
  </main>
</div>

<style lang="scss">
  :root {
    --header-height: 68px;
  }
  header {
    position: fixed;
    height: var(--header-height);
    padding: 1rem;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    > div {
      display: flex;
      align-items: center;
    }
  }
  h1 {
    font-size: 1.4rem;
    margin: 0;
    &.is-mobile {
      font-size: 1rem;
    }
  }
  .menu-button {
    margin: 0;
    width: 40px;
    margin-right: 1rem;
  }
  .search-input {
    width: 320px;
    padding-left: 0.75rem;
    margin: 0;
    &.is-mobile {
      width: calc(100vw - 1rem - var(--header-height));
    }
  }

  .search-input-wrapper {
    &.is-mobile {
      position: absolute;
      right: .5rem;
    }
  }
  .clear-input {
    left: -40px;
    position: relative;
    z-index: 100;
    width: 35px;
    margin: 0;
    border: none;
    background: transparent;
    margin-right: -30px;
    &:focus {
      outline: auto;
    }
  }
  .wrapper {
    display: flex;
    height: calc(100%);
    padding-top: var(--header-height);
  }
  nav {
    direction: rtl;
    overflow: auto;
    overflow-x: hidden;
    min-width: 220px;
    height: 100%;
    padding: 0.25rem;

    .nav-inner {
      display: flex;
      flex-direction: column;
      direction: ltr;
    }

    a {
      padding: 0.5rem 1rem;
      text-decoration: none;
      font-weight: 600;
      // color: $blue;
      &:hover,
      &:focus {
        background-color: #eee;
      }
      &:active {
        background-color: #ddd;
      }
    }
    &.is-mobile {
      box-shadow: 40px 0px 35px -35px rgb(0 0 0 / 10%);
    }

    .link-prefix {
      opacity: 0.3;
    }
  }
  main {
    overflow-x: hidden;
    padding: 0 1rem 4rem 1rem;
    &.is-blocked {
      // pointer-events: none;
      user-select: none;
    }
  }
  .anchor {
    margin-top: 10px;
    margin-bottom: -10px;
    height: 10px;
  }
  article {
    padding: 1rem 2rem;
    margin-top: 1rem;
    box-shadow: 0 0 15px 0 rgb(0 0 0 / 5%);
    background: white;
  }
  .article-header {
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    background: white;
    padding: 1rem 0;
  }
  .article-version {
    background-color: #eee;
    font-weight: 700;
    border-radius: 14px;
    padding: 0 0.5rem;
    margin-left: 1rem;
  }
  .article-content {
    overflow: auto;
  }

  @media screen and (max-width: 799px) {
    nav {
      position: fixed;
      left: 0;
      width: 280px !important;
      max-width: none !important;
      bottom: 0;
      height: calc(100% - 68px);
      background: var(--page-bg);
      z-index: 1;
    }
    main {
      width: 100% !important;
    }

    .menu-button {
      display: block;
    }
  }
</style>
